import React, { useEffect, useState } from "react";
import CountUp from "react-countup"
import download from "../../assets/images/bg/download.jpeg";
import {
  Button,
  ButtonGroup,
  Card,
  CardImg,
  CardBody,
  CardTitle,
  Row,
  Col,
} from "reactstrap";
import { countapi } from "../../api/user";
const Buttons = () => {
  const [count , setCount] = useState('')
useEffect(()=>{
  async function callapi(){
    try{
   const response = await countapi();
   console.log(response)
   setCount(response)
    }catch(error){
      console.log(error)
    }
  }
  callapi();
},[])
  const totalCourses = 10;
  const totalUsers = 50;
  return (
    <div className="admin-dashboard caliber-font">
    <h1 className="dashboard-title">Admin Dashboard</h1>
    <div className="counting-container">
      <div className="counting-item">
        <div className="counting-number"><CountUp start={0} end={count.courseCount} duration={3} delay={0.5}/></div>
        <div className="counting-label">Total Courses</div>
      </div>
      <div className="counting-item">
        <div className="counting-number"><CountUp start={0} end={count.userCount - 1} duration={3} delay={0.5}/></div>
        <div className="counting-label">Total Users</div>
      </div>
      <div className="counting-item">
        <div className="counting-number"><CountUp start={0} end={count.trainingPlanCount} duration={3} delay={0.5}/></div>
        <div className="counting-label">Total TrainingPlans</div>
      </div>
    </div>
  </div>
  );
};
export default Buttons;